
/* Responsive Styling */

@media (max-width: 600px) {
  .container {
    padding: 15px;
  }

  button {
    width: 100%;
  }

  .result-actions {
    flex-direction: column;
    align-items: stretch;
  }
}



body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f7fa;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 600px;
  margin: 50px auto;
  background-color: #ffffff;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

h1 {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1.5rem;
  color: #333;
}

form {
  display: flex;
  gap: 10px;
  margin-bottom: 1rem;
}

input[type="text"] {
  flex: 1;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 8px;
}

/* Submit button */

button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 16px;
  font-size: 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s ease;
}

button:hover {
  background-color: #0056b3;
}

/* Result box */

.result-box {
  margin-top: 1.5rem;
  padding: 1rem;
  background-color: #f0f8ff;
  border-radius: 8px;
  border: 1px solid #ddd;
}

.result-box div {
  margin-bottom: 8px;
  font-size: 16px;
}

/* Clear Button */

.clear-button {
  margin-top: 20px;
  padding: 10px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.2s ease;
}

.clear-button:hover {
  background-color: #0056b3;
}

/* Saved results*/

.saved-results-list li {
  position: relative;
  margin-bottom: 20px; 
  padding-right: 40px; 
}

/* Delete button */

.saved-results-list button.delete-btn {
  position: absolute;
  right: 0;
  top: 0;
  color: white;
  border: none;
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  /* line-height: 1; */
  transition: background-color 0.2s ease;

}

/* voice button */

.voice-button {
  padding: 8px 12px;
  background-color: #007bff;
  color: white;
  font-size: 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.voice-button:hover {
  background-color: #0056b3;
}

/* Error Box */

.error-box {
  background-color: #ffe0e0;
  color: #a94442;
  padding: 10px 15px;
  border-left: 5px solid #f44336;
  margin-bottom: 15px;
  border-radius: 5px;
}

/* Download Button */

.download-btn {
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.download-btn:hover {
  background-color: #45a049;
}


